.btn-wrapper {
    @apply flex;

    .btn {
        @apply block first:mr-0 mr-2;

        &.btn-primary,
        &.btn-secondary,
        &.btn-contrast,
        &.btn-neutral,
        &.btn-danger,
        &.btn-warning,
        &.btn-success,
        &.btn-light {
            @apply block first:ml-0 ml-2;
        }
    }
}

.btn {
    @apply antialiased px-4 py-3 rounded-[8px] flex items-center justify-center text-center text-base text-neutral-100 bg-contrast no-underline font-main space-x-3 transition-all border-none appearance-none cursor-pointer outline-none stroke-white fill-white;
    &.btn-inline-block {
        @apply inline-block;
    }

    &.btn-square {
        @apply aspect-square p-3 justify-center items-center;
    }

    &.btn-primary {
        @apply bg-primary;

        .btn-label {
            @apply text-white;
        }
        .notification-light {
            & > .notification-value {
                @apply text-primary;
            }
        }
    }

    &.btn-secondary {
        @apply bg-secondary hover:bg-secondary-600 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-secondary;

        .btn-label {
            @apply text-white;
        }
        .notification-light {
            & > .notification-value {
                @apply text-secondary;
            }
        }
    }

    &.btn-contrast {
        @apply bg-contrast hover:bg-contrast-600 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-contrast;

        .btn-label {
            @apply text-white;
        }
        .notification-light {
            & > .notification-value {
                @apply text-contrast;
            }
        }
    }

    &.btn-neutral {
        @apply bg-neutral hover:bg-neutral-800 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-neutral;

        .btn-label {
            @apply text-white;
        }
    }

    &.btn-danger {
        @apply bg-danger hover:bg-danger-800 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-danger;

        .btn-label {
            @apply text-white;
        }
        .notification-light {
            & > .notification-value {
                @apply text-danger;
            }
        }
    }

    &.btn-warning {
        @apply bg-warning hover:bg-warning-600 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-warning;

        .btn-label {
            @apply text-white;
        }
    }

    &.btn-success {
        @apply bg-success hover:bg-success-800 disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-success;

        .btn-label {
            @apply text-white;
        }
    }

    &.btn-light {
        @apply bg-white text-contrast hover:bg-neutral-200 stroke-contrast fill-contrast disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;

        .btn-label {
            @apply text-contrast;
        }
    }

    &.btn-border {
        @apply border border-solid bg-transparent p-[calc(0.75rem-1px)];

        &.btn-primary {
            @apply border-primary text-primary stroke-primary fill-primary;
            &:after {
                @apply bg-primary;
            }
        }

        &.btn-secondary {
            @apply border-secondary text-secondary hover:bg-secondary-600/10 stroke-secondary fill-secondary disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
            &:after {
                @apply bg-secondary;
            }
        }

        &.btn-contrast {
            @apply border-contrast text-contrast hover:bg-contrast/10 stroke-contrast disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
            &:after {
                @apply bg-contrast;
            }
        }

        &.btn-neutral {
            @apply border-neutral text-neutral hover:bg-neutral-800/10 stroke-neutral fill-neutral disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
            &:after {
                @apply bg-neutral;
            }
        }

        &.btn-danger {
            @apply border-danger text-danger hover:bg-danger-800/10 stroke-danger fill-danger disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
            &:after {
                @apply bg-danger;
            }
        }

        &.btn-warning {
            @apply border-warning text-warning hover:bg-warning-600/10 stroke-warning fill-warning disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
            &:after {
                @apply bg-warning;
            }
        }

        &.btn-success {
            @apply border-success text-success hover:bg-success-800/10 stroke-success fill-success disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
            &:after {
                @apply bg-success;
            }
        }

        &.btn-light {
            @apply border-white text-white hover:bg-white/10 stroke-white fill-white disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white/10;
            &:after {
                @apply bg-white;
            }
        }
    }

    &.btn-ghost {
        @apply border-none bg-transparent;

        &.btn-primary {
            @apply text-primary stroke-primary fill-primary;
        }

        &.btn-secondary {
            @apply text-secondary hover:bg-secondary-600/10 stroke-secondary fill-secondary disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
        }

        &.btn-contrast {
            @apply text-contrast hover:bg-contrast/10 stroke-contrast fill-contrast text-danger disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
        }

        &.btn-neutral {
            @apply text-neutral hover:bg-neutral-800/10 stroke-neutral fill-neutral disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
        }

        &.btn-danger {
            @apply text-danger hover:bg-danger-800/10 stroke-danger fill-danger disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
        }

        &.btn-warning {
            @apply text-warning hover:bg-warning-600/10 stroke-warning fill-warning disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-white;
        }

        &.btn-success {
            @apply text-success hover:bg-success-800/10 stroke-success fill-success disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-transparent;
        }

        &.btn-light {
            @apply text-white hover:bg-white/10 stroke-white fill-white disabled:opacity-50 disabled:cursor-not-allowed disabled:bg-transparent;
        }
    }

    &.btn-border,
    &.btn-ghost {
        &.btn-primary .btn-label {
            @apply text-primary;
        }

        &.btn-secondary .btn-label {
            @apply text-secondary;
        }

        &.btn-contrast .btn-label {
            @apply text-contrast;
        }

        &.btn-neutral .btn-label {
            @apply text-neutral;
        }

        &.btn-danger .btn-label {
            @apply text-danger;
        }

        &.btn-warning .btn-label {
            @apply text-warning;
        }

        &.btn-success .btn-label {
            @apply text-success;
        }

        &.btn-light .btn-label {
            @apply text-neutral-100;
        }
    }

    &.btn-lg {
        @apply text-lg px-5 tracking-wide;
        &.btn-square {
            @apply p-3;
        }
    }

    &.btn-sm {
        @apply text-sm px-3 py-2 space-x-2;
        &.btn-square {
            @apply p-2;
        }
    }

    &.btn-xs {
        @apply text-xs p-2 tracking-tight space-x-1;
        &.btn-square {
            @apply p-2;
        }
    }

    .btn-label {
        @apply self-center text-white;
    }

    .btn-icon,
    .notification {
        @apply self-center;
    }
}
